___CSS_LOADER_EXPORT___.push([module.id, `.set_container {
    width: fit-content;
    position: relative;
    line-height: 18pt;
    min-height: 18pt;
    cursor: pointer;
    
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.highlighted_node {
    transition: drop-shadow 0.1s;
    filter: drop-shadow(0 0 0.14em #333);
}

.set_too_high {
    overflow-y: clipped;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 2.5em;
    min-height: 2.5em;
}

.set_value {
    width: fit-content;
    margin: 0.38em 1em 0.38em 1em;

    position: relative;
    flex: 0 0 auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.mini {
    text-align: center;
    vertical-align: middle;
    width: fit-content;
    height: fit-content;
    /* font-size: 12pt; */
    min-width: 0.57em;
    cursor: pointer;
    min-height: 0.57em;
}

.recursion_button {
    position: absolute;
    right: 0;
    top: 0;
}

.recursion_button:hover {
    box-shadow:
        0 0.28em 0.57em 0 rgba(0, 0, 0, 0.2),
        0 0.43em 0.71em 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s ease-in-out;
    -moz-transition: 0.3s ease-in-out;
    -webkit-transition: 0.3s ease-in-out;
}

.recursion_button_text {
    cursor: pointer;
    font-size: 1pt;
    font-weight: bold;
    border-radius: 0em 0.57em 0em 0.28em;
    padding: 1px;
    vertical-align: middle;
    text-align: center;
    width: auto;
}

@keyframes loading {
    0% {
        background-position: -200em 0;
    }

    100% {
        background-position: calc(200em + 100%) 0;
    }
}

.loading {
    background: linear-gradient(90deg, #ffffff 25%, #e0e0e0 50%, #ffffff 75%);
    background-size: 200em 100%;
    border-radius: 0.8em;
    animation: loading 1.5s infinite;
}

.hidden {
    visibility: hidden;
}

.rotate_icon {
    transform: rotate(180deg);
}
